<template>
    <div class="lessonSelect">
        <div class="header">
            <span @click="back" class="back">
                <img src="../../assets/img/back.png" class="releaseIcon">
            </span>
            <span class="usersTitle">选择课程</span>
            <span class="back"></span>
        </div>
        <div class="lessonArea">
            <ul class="lessons">
                <li class="lesson" :class="{'edited': lesson.state === true}" v-for="(lesson, index) in lessons" :key="index" @click="editLesson(index)">
                    <span>第{{ index + 1 }}节课</span>
                    <span class="lessonTime">
                      <span class="time">{{ lesson.time }}</span>
                      <span class="next">
                        <img src="../../assets/img/next.png">
                      </span>
                    </span>
                </li>
            </ul>
        </div>
    </div>
</template>

<script>
import store from '../../vuex/store'
export default {
  name: 'lessonSelect',
  data () {
    return {
      lessons: []
    }
  },
  mounted () {
    this.lessons = store.state.lessonEdit
    console.log(store.state.lessonEdit)
  },
  methods: {
    back () {
      store.commit('setLessonDate', '')
      this.$router.go(-1)
    },
    editLesson (index) {
      store.commit('saveLessonTime', this.lessons[index].time)
      this.lessons[index].state = true
      store.commit('saveLessons', this.lessons)
      this.$router.push('/home/mine/lessonManage')
    }
  }
}
</script>

<style scoped>
  * {
    margin: 0;
    padding: 0;
    font-size: 0.48rem;
  }

  .header {
    height: 1.5rem;
    display: flex;
    justify-content: space-between;
    position: relative;
  }

  .header span {
    display: flex;
    justify-content: center;
    padding: 0.5rem;
    line-height: 0.5rem;
  }

  .back {
    height: 0.5rem;
    width: 0.266667rem;
  }

  .releaseIcon {
    width: 100%;
  }

  img {
    width: 100%;
  }

  .lessonArea {
    width: calc(100% - 0.8rem);
    margin: 0 auto;
  }

  .lessons {
    list-style: none;
  }

  .lesson {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 1.44rem;
    border-bottom: 1px solid #f5f5f5;
    font-size: 0.42667rem;
    color: #333333;
  }

  .next {
    display: flex;
    align-content: center;
    justify-content: center;
    width: 0.21132rem;
    height: 0.4rem;
  }

  .lessonTime {
    display: flex;
    align-items: center;
  }

  .time {
    margin-right: 0.32rem;
    font-size: 0.4rem;
    color: #666666;
  }

  .edited {
    background: #ffd200;
  }
</style>



